import { mapGetters } from 'vuex';

  export default {
  name: 'doll',
  data () {
    return {
      items: [],
    }
  },
  methods: {
    init: function() {
      let jwt = this.getJwt;

      if(jwt.length === 0) {
        alert("");
        return;
      }

      this.$http({
        method: 'GET',
        url: "https://ucast.cc/api/v1/appclaw/my-rounds?win=true",
        headers: {
          Authorization: "bearer " + jwt
        }
      }).then(res => {
          this.items = res.body;
        },
        res => {
          alert("~");
        });
    }
  },
  computed: {
    ...mapGetters([
      'getJwt',
    ])
  },
  created: function() {
    this.$nextTick(this.init, 100);
    document.title = '';

  }
}



