import { mapGetters } from 'vuex';

export default {
  name: 'detail',
  data () {
    return {
        history: [],
    }
  },
  methods: {
    init: function() {
      /* jwt */
      let jwt = this.getJwt;
//      console.log(jwt);

      if(jwt.length === 0) {
        alert("jwt");
        return;
      }

      this.$http({
        method: 'GET',
        url: "https://ucast.cc/api/v1/appclaw/games/10003",
        headers: {
          Authorization: "bearer " + jwt
        }
      }).then(res => {
//          console.log(res);
          this.history = res.body.history;
        },
        res => {
          alert("~");
      });
    }
  },
  computed: {
    ...mapGetters([
      'getJwt',
    ])
  },
  created: function() {
//    console.log("created!");
    this.$nextTick(this.init, 100);
  }
}



