export default {
  name: 'scan',
  data () {
    return {
      qrCode: "Hi,I'm code."
    }
  },
  methods: {
    onScan: function () {
      let self = this;

      //native
      window.onQRCodeScanRet = (ret)=>{
        delete window.onQRCodeScanRet;
        self.qrCode = ret;

        if(ret == "hahauCast"){
          setTimeout(()=>{
            self.$router.push({ path: 'list', query: {}});
          }, 200);}
      }

      //
      window.WebViewJavascriptBridge.callHandler(
        'onScanQrcode',
        0,
        (ret) => {console.log(ret);}
      );
    }
  },
  created: function() {
    document.title = '##';
  }

}



