import { mapGetters } from 'vuex';

export default {
  name: 'home',
  data () {
    return {
        lists: [
                {
                  name: "",
                  tel: "18501053820",
                  addr: "",
                  street: "2",
                  pos: "100085"
                },
                {
                  name: "",
                  tel: "18501053820",
                  addr: "",
                  street: "",
                  pos: "100085"
                }
        ],
    }
  },
  methods: {
    onBack: function () {
      this.$router.back(-1);
    },
    addAddress: function () {
      this.$router.replace('/my/my_address/add_address');
//      this.$router.replace('/address');
    },
    dataInit: function () {
      let index = this.$route.params.index;
//        console.log(index);
      if(typeof(index) !== "undefined") {
        this.lists.splice(index, 1,this.$route.params.list);
      }
    },
    init: function() {
      this.dataInit();
      /* jwt */
      let jwt = this.getJwt;
//      console.log(jwt);

      if(jwt.length === 0) {
        alert("jwt");
        return;
      }

//      this.jwt = this.getJwt('jwt') || "";
//      if (!this.jwt) {
//        alert("~");
//        return;
//      }
      //console.log(this.jwt);
      this.$http({
        method: 'GET',
        url: "https://ucast.cc/api/v1/games",
        headers: {
          Authorization: "bearer " + this.jwt
        }
      }).then(res => {
//          console.log(res);
          this.glbList = res.body;
        },
        res => {
          alert("~");
      });
    }
  },
  computed: {
    ...mapGetters([
      'getJwt',
    ])
  },
  created: function() {
//    console.log("created!");
//    this.$nextTick(this.init, 100);
    this.$nextTick(this.dataInit, 100);
  }
}



