import { mapGetters } from 'vuex';

  export default {
  name: 'recharge',
  data () {
    return {
        sel: 0,
        items: [
          {
            comment: "",
            coins: 50,
            charge: 5
          },
          {
            comment: "20",
            coins: 120,
            charge: 10
          },
          {
            comment: "60",
            coins: 260,
            charge: 20
          },
          {
            comment: "200",
            coins: 700,
            charge: 50
          }
        ],
    }
  },
  methods: {
    onSelect: function (index) {
      this.sel = index;
    },
    onCharge: function () {
      let fee = this.items[this.sel].charge;
      let path = "https://ucast.cc/api/v1/apay/pay?v=" + fee;
      let jwt = this.getJwt;
      if(!jwt) {
        alert("");
        return;
      }
      this.$http({
        method: 'GET',
        url: path,
        headers: {
          Authorization: "bearer " + jwt
        }
      }).then(res => {
          if(res.body.url){
            window.location.href = res.body.url;
            console.log(res.body.url);
          }
          else if(res.message)
            alert(res.message);
          else
            alert("~");
        },
        res => {
          alert("~");
        });

    },


    init: function() {

    }
  },
  computed: {
    ...mapGetters([
      'getJwt',
    ])
  },
  created: function() {
//    console.log("created!");
//    this.$nextTick(this.init, 100);
    document.title = '';

  }
}



