  import Group from 'vux/src/components/group/';
  import XAddress from 'vux/src/components/x-address/';
  import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'

  export default {
  name: 'edit',
  components: {
    Group,
    XAddress,
    ChinaAddressV3Data,
  },
  data () {
    return {
      list: {},
      index: null,
      value: [],
      addressData: ChinaAddressV3Data,
    }
  },
  methods: {
    onsel: function () {
      console.log(this.list.name);
    },
    onBack: function () {
      this.$router.back(-1);
    },
    saveAddress: function () {

    },
    getJwt: function(name) {
      var results = new RegExp('[\?&]' + name + '=([^&#]*)').exec(window.location.href);
      if (results)
        return results[1] || 0;
      else
        return null;
    },
    init: function() {
      /* jwt */
      this.jwt = this.getJwt('jwt') || "";
      if (!this.jwt) {
        alert("~");
        return;
      }
      //console.log(this.jwt);
      this.$http({
        method: 'GET',
        url: "https://ucast.cc/api/v1/games",
        headers: {
          Authorization: "bearer " + this.jwt
        }
      }).then(res => {
//          console.log(res);
          this.glbList = res.body;
        },
        res => {
          alert("~");
      });
    }
  },
  created: function() {
//    console.log(this.$router);
//    console.log("created!");
//    this.$nextTick(this.init, 100);
    this.$nextTick(function () {
      this.list = this.$route.params.list;
      this.index = this.$route.params.index;
      console.log(this.index);
    }, 100);
  }
}



