import { mapGetters } from 'vuex';

export default {
  name: 'home',
  data () {
    return {
      mine:{}
    }
  },
  methods: {
    onSelect: function () {
      console.log(this.value);
    },
    init: function () {

      let jwt = this.getJwt;
      if(!jwt) {
        alert("");
        return;
      }

      this.$http({
        method: 'GET',
        url: "https://ucast.cc/api/v1/me",
        headers: {
          Authorization: "bearer " + jwt
        }
      }).then(res => {
          //console.log(res.body);
          this.mine = res.body;
        },
        res => {
          alert("~");
        });

    }
  },
  computed: {
    ...mapGetters([
      'getJwt',
    ])
  },
  created: function() {
    this.$nextTick(this.init, 100);
    document.title = '';
  }
}



